const imageIsExist = function (url) {
    return new Promise((resolve, reject) => {
        // 
        const image = new Image()
        // 
        image.src = url
        //  HTML  load

        image.onload = function () { 
            // 
            if (this.complete === true) {
                resolve(image)
            }
        }
        image.onerror = function () { // 
            reject('could not load image')
        }
    })
}

export const mydirective = {
    install(app) {
        app.directive('image', (el, binding) => {
            let placeholderImage = require('@/assets/logo.png') // 
            let defaultImage = el.getAttribute('default-img') // 
            let errorImage = el.getAttribute('error-img') // 

            // 300
            const times = setTimeout(() => {
                el.setAttribute('src', defaultImage || placeholderImage)
            }, 300);
            // el.setAttribute('class', 'img-objectfix-contain') 

            let realImageUrl = binding.value //  v-image="cover"  cover

            if (realImageUrl) {
                imageIsExist(realImageUrl)
                    .then((res) => {
                        clearTimeout(times)
                        el.setAttribute('src', realImageUrl)
                    })
                    .catch(() => {
                        el.setAttribute('src', errorImage || placeholderImage)
                    })
            }
        })
    }
}