import {
    throttle
} from '@/utils/throttle'
import {
    commentStar
} from "~/network";

// 
/**
 * 
 * @param {Number} delay 
 * @returns {Function} 
 */
export function useHandleStar(delay) {
    function foo(id, cid, t, type, callback) {
        commentStar(id, cid, t, type).then((res) => {
            callback(res)
        })
    }
return throttle(foo, delay)
}

