import {ref} from 'vue';
/**
 * 
 * @param {*} dom dom
 * @returns boolean
 */

export function watchMouse(dom) {
    // 
    const isShow = ref(true)
    dom.addEventListener('mouseover',(e) => {
        isShow.value = false
    })
    // 
    dom.addEventListener('mouseleave',(e) => {
        isShow.value = true
    })

    return isShow
}