import { computed, defineComponent, ref, watch } from "vue";
import { formatSongTime } from "@/utils/formatSongTime";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { mapMusicInfo } from "@/hook/mapMusicInfo";
import loading from "@/components/loading.vue";
export default defineComponent({
  name: "",
  components: {},
  props: {
    // 
    isHandleShow: {
      type: Boolean,
      default: true
    },
    // 
    showHeader: {
      type: Boolean,
      default: true
    },
    tabStyle: {
      type: Object,
      default: () => {
        return {
          fontSize: "13px",
          height: "40px"
        };
      }
    },
    // 
    songList: {
      type: Array,
      required: true
    },
    // 
    // isAlbumShow: {
    //   type: Boolean,
    //   default: true,
    // },
    // 
    isIndexShow: {
      type: Boolean,
      default: true
    },
    // 
    showImg: {
      type: Boolean,
      default: false
    },
    propList: {
      type: Array,
      default: () => {
        return [{
          prop: "author",
          label: "",
          slotName: "author"
        }, {
          prop: "album",
          label: "",
          "min-width": "50",
          slotName: "album"
        }, {
          prop: "time",
          label: "",
          align: "right",
          "header-align": "right"
        }];
      }
    }
  },
  emits: ["onhandleClick"],

  setup(props, {
    emit
  }) {
    const store = useStore();
    const route = useRoute(); // 

    const centerDialogVisible = ref(false); // 

    const isCurrentPlayMusic = ref(false); // 

    const img = require("../assets/image/activity-play.png"); // 


    const songlist = computed(() => {
      if (props.songList) {
        return mapMusicInfo(props.songList);
      }
    }); // 

    const changeClick = (row, column) => {
      // 
      // store.dispatch("playMusic/isMusicPlay", row.id).then((res) => {
      //   if (!res.success) {
      //     centerDialogVisible.value = true;
      //     return;
      //   }
      // });
      // 
      emit("onhandleClick", row, props.songList);
    }; // 


    const songlistindex = ref(); // 

    const songlistcurrent = ref();
    /**
     * @param {number} currentSongList 
     * @param {Object} currentSong 
     */

    const actionMusic = (currentSongList, currentSong) => {
      const index = currentSongList.findIndex(item => item.id === currentSong); // 

      if (index !== -1) {
        isCurrentPlayMusic.value = true;
        songlistindex.value = index + 1;
      } else {
        isCurrentPlayMusic.value = false;
      }
    };

    watch( //
    () => songlist.value, value => {
      songlistcurrent.value = value; // 

      actionMusic(value, store.state.playMusic.musicInfo.id);
    }, {
      immediate: true
    }); // 

    watch(() => store.state.playMusic.musicInfo, value => {
      actionMusic(songlistcurrent.value, value.id);
    }); // hook

    const {
      likelist,
      uselike
    } = require("@/hook/useLike.js"); // 


    const loveClick = id => {
      uselike(id);
    }; // 


    const dislike = id => {
      uselike(id, false);
    };

    return {
      changeClick,
      songlist,
      formatSongTime,
      img,
      centerDialogVisible,
      songlistindex,
      isCurrentPlayMusic,
      songlistcurrent,
      likelist,
      loveClick,
      dislike
    };
  }

});