import {
    groupList,
    categoryList,
    getVideo,
    videoAll,
    videoDetail,
    videoUrl,
    mvUrl,
    mvDetail,
    relatedVideo,
    relatedMv,
    mvComment,
    voideComment
} from '~/network'

export const video = {
    namespaced: true,
    state: {
        grouplist: [],
        // 
        tags: {
            id: 58100,
            name: ''
        },
    },
    getters: {
        getTags(state) {
            return state.tags
        }
    },
    mutations: {
        // 
        setTags(state, tags) {
            state.tags = tags
        },
    },
    actions: {
        // 
        async getCategoryList() {
            const result = await categoryList()
            return result.data
        },

        // 
        async getGroupList() {
            const result = await groupList()
            return result.data
        },
        // /
        async getVideoGroups({
            commit
        }, {
            id,
            offset
        }) {
            const result = await getVideo(id, offset)
            return result.datas
        },
        // 
        async getVideoAll({
            commit
        }, {
            offset
        }) {
            const result = await videoAll(offset)
            return result
        },
        // 
        async getVideoDetail({
            commit
        }, vid) {
            const result = await videoDetail(vid)
            return result
        },
        // 
        async getVideoUrl({
            commit
        }, vid) {
            const result = await videoUrl(vid)
            return result
        },
        // MV
        async getMvUrl({
            commit
        }, id) {
            const result = await mvUrl(id)
            return result
        },
        // mv
        async getMvData({
            commit
        }, id) {
            const result = await mvDetail(id)
            return result
        },
        // 
        async getRelatedVideo({
            commit
        }, id) {
            const result = await relatedVideo(id)
            return result
        },
        // MV
        async getRelatedMv({
            commit
        }, id) {
            const result = await relatedMv(id)
            return result
        },
        // MV
        async getMvComment({
            commit
        }, {
            id,
            limit,
            offset
        }) {
            const result = await mvComment(id, limit, offset)
            console.log(result);
            return result
        },
        // 
        async getVoideComment({
            commit
        }, {
            id,
            limit,
            offset
        }) {
            const result = await voideComment(id, limit, offset)
            console.log(result)
            return result
        }
    }
}