import { useStore } from "vuex";
import { computed, ref } from "vue";
import { songLike } from "~/network";
const store = useStore(); // id

const useruid = computed(() => {
  const result = store.state.login.account;
  return result.id || null;
});
const likelist = ref([]); // 

const funlicklist = () => {
  if (useruid) store.dispatch("login/getLikeList", useruid.value).then(res => {
    likelist.value = res;
  });
};

funlicklist();
/**
 * 
 * @param {*} id id 
 * @param {*} like ture false
 */

const uselike = async (id, like = true) => {
  const result = await songLike(id, like);
  funlicklist();

  if (result.code === 200 && like == true) {
    ElMessage({
      message: "",
      grouping: true,
      type: "success"
    });
  } else if (like == false) {
    ElMessage({
      message: "",
      grouping: true,
      type: "success"
    });
  }
};

export { likelist, uselike };